@import url(https://fonts.googleapis.com/css2?family=Prompt&display=swap);
*,html,body{padding:0;margin:0;box-sizing:border-box;font-family:'Prompt', sans-serif;font-size:12px;color:white}.container{display:flex;justify-content:center;align-items:center;flex-direction:column;height:100vh;padding:1.5rem;background-color:black}@keyframes pulsate{100%{text-shadow:0 0 4px #fff, 0 0 11px #fff, 0 0 19px #fff, 0 0 40px #bc13fe, 0 0 80px #bc13fe, 0 0 90px #bc13fe, 0 0 100px #bc13fe, 0 0 150px #bc13fe}0%{text-shadow:0 0 2px #fff, 0 0 4px #fff, 0 0 6px #fff, 0 0 10px #bc13fe, 0 0 45px #bc13fe, 0 0 55px #bc13fe, 0 0 70px #bc13fe, 0 0 80px #bc13fe}}@keyframes flicker{0%,18%,22%,25%,53%,57%,100%{text-shadow:0 0 4px #fff, 0 0 11px #fff, 0 0 19px #fff, 0 0 40px #0fa, 0 0 80px #0fa, 0 0 90px #0fa, 0 0 100px #0fa, 0 0 150px #0fa}20%,24%,55%{text-shadow:none}}.text{text-align:center;font-size:3rem;margin:3rem 0rem}.text span{font-weight:bold;font-size:3rem;color:goldenrod;font-style:italic;padding:0 0.5rem}.text.top{animation:flicker 1.5s infinite alternate;font-size:4rem;text-shadow:0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff, 0 0 42px yellow, 0 0 82px yellow, 0 0 92px yellow, 0 0 102px yellow, 0 0 151px yellow}.timer{display:flex;align-items:center;justify-content:center;flex-direction:column;margin:1rem;width:7rem;padding:1rem;box-shadow:0 0 0.2rem #fff, 0 0 0.2rem #fff, 0 0 2rem #bc13fe, 0 0 0.8rem #bc13fe, 0 0 2.8rem #bc13fe, inset 0 0 1.3rem #bc13fe}.timer__container{display:flex;flex-wrap:wrap;align-items:center;justify-content:center}.value{font-size:3rem;color:#fff;text-shadow:0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff, 0 0 42px #bc13fe, 0 0 82px #bc13fe, 0 0 92px #bc13fe, 0 0 102px #bc13fe, 0 0 151px #bc13fe}.label{font-size:2rem}

